<template>
<!-- 商品卡片 -->
  <div class="card-container van-hairline--bottom">
    <div class="card-img">
      <img :src="images[0]" ref="img" />
    </div>
    <div class="card-content">
      <div class="title overflow-hidden">{{ title }}</div>
      <div class="desc overflow-hidden">{{ desc }}</div>
      <div class="tags">
        <div v-for="item in tags" :key="item">{{ item }}</div>
      </div>
      <div class="prices">￥{{ price }}</div>
      <div class="counter">
        <div v-if="num">
          <img
            src="https://duyi-bucket.oss-cn-beijing.aliyuncs.com/img/rec.png"
            @touchend="counter(id, -1)"
            alt=""
          />
        </div>
        <div class="num" v-if="num">
          {{ num }}
        </div>
        <div>
          <img
            src="https://duyi-bucket.oss-cn-beijing.aliyuncs.com/img/add.png"
            @touchend="counter(id, +1)"
            alt=""
          />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Animate from "@/tools/Animate";
export default {
  props: ["images", "tags", "title", "desc", "price", "id", "num", "onify"],
  methods: {
    counter(id, num) {
      this.$store.commit("storageChange", { id, value: num });
      if (num == -1 || this.onify) {
        return;
      }
      // 获取图片的位置和宽高
      const { top: startY, left: startX } =
        this.$refs.img.getBoundingClientRect();
      const { offsetWidth: imgWidth, offsetHeight: imgHeight } = this.$refs.img;
      //获取购物车元素
      const shop = document.getElementById("shop-card");
      // 获取购物车位置和宽高
      const { top: sTop, left: sLeft } = shop.getBoundingClientRect();
      const { offsetWidth: shopWidth, offsetHeight: shopHeight } = shop;
      //   图片移动的结束位置
      const endX = sLeft + shopWidth / 2;
      const endY = sTop + shopHeight / 2;
      Animate({
        startY,
        startX,
        width: imgWidth,
        height: imgHeight,
        src: this.$refs.img.src,
        endX,
        endY,
      });
    },
  },
};
</script>
<style lang="less" scoped>
.card-container {
  width: 100%;
  display: flex;
  height: 100px;
  .card-img {
    width: 90px;
    margin-right: 20px;
    img {
      width: 90px;
      height: 90px;
    }
  }
  .card-content {
    flex: 1 1 auto;
    position: relative;
    > div {
      width: 170px;
    }
    .title {
      font-size: 13px;
      color: #1a1a1a;
      margin-top: 5px;
    }
    .desc {
      color: #aaa;
      font-size: 11px;
      margin-top: 5px;
    }
    .tags {
      display: flex;
      margin-top: 4px;
      > div {
        font-size: 10px;
        padding: 2px;
        color: #aaa;
        border-radius: 3px;
        margin-right: 5px;
        border: 1px solid #eee;
      }
    }
    .prices {
      color: #ff1a90;
      font-size: 14px;
      font-weight: 600;
      margin-top: 4px;
    }
    .counter {
      position: absolute;
      bottom: 12px;
      right: 15px;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      > div:not(.num) {
        width: 16px;
        height: 16px;
        img {
          width: 100%;
        }
      }
      .num {
        padding: 0 5px;
        height: 22px;
        line-height: 22px;
      }
    }
  }
}
.overflow-hidden {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>